<template>
  <swiper :options="swiperOption2" id="songslider">
    <!-- slides -->
    <swiper-slide class="songwrap">
      <header>
        <div>—— {{singer}} ——</div>
      </header>
      <div></div>
      <footer>
        <div>{{line}}</div>
      </footer>
    </swiper-slide>
    <swiper-slide class="songwrap">
      2222222
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>

</template>

<script>
  import { mapState, mapMutations } from 'vuex'
  export default {
    name: 'songdetail',
    components: {
    },
    props: ['alllyric', 'line'],
    data () {
      return {
        swiperOption2: {
          onSlideChangeStart (swiper) {
            // alert(swiper.activeIndex);// 切换结束时，告诉我现在是第几个slide
            let pic = document.querySelectorAll('#btPic')[0];
            if (swiper.activeIndex !== 0) {
              pic.style.display = 'none'
            } else {
              pic.style.display = 'block'
            }
          },
          pagination: '.swiper-pagination',
          paginationClickable: true
        }
      }
    },
    created () {
    },
    mounted  () {
    },
    watch: {
      line: function (newValue, oldValue) {
        this.line = newValue;
        console.log(this.line)
      }
    },
    methods: {
      ...mapMutations(['changemusic'])
    },
    computed: {
      ...mapState(['music', 'singer', 'songname', 'lyric', 'musicUrl', 'musicPic'])
    }
  }
</script>
<style rel="stylesheet/less" lang="less">
@import "../assets/less/var.less";
#songslider{
  flex: 1;
  margin: 0;
  .songwrap{
    color: #fff;
    display: flex;
    height: calc(~'100vh - 170px');
    flex-direction: column;
    justify-content: space-between;
    header{
      div{
        font-size: .75rem;
      }
    }
    footer{
      color: @green;
      margin-bottom: 10px;
    }
  }
  .swiper-pagination-bullet-active{
    background: #fff !important;
    opacity: 1;
  }
}
</style>



