const mutations = {
  changeMusic (state, obj) {
    state.songname = obj.songname;
    state.singer = obj.singer;
    state.lyric = obj.lyric;
    state.musicUrl = obj.musicUrl;
    state.musicPic = obj.musicPic;
    for (let i of state.music) {
      if (i.songname === obj.songname && i.singer === obj.singer) {
        // state.music.splice(index, 1);
        return;
      }
    }
    state.music.unshift(obj);
  }
};
export default mutations



