<template>
  <div id="topmenu">
    <section id="main">
      <span><i class="fa fa-navicon"></i></span>
      <div class="topmenu">
        <router-link :to="'mine'"><span @click="choose">我的</span></router-link>
        <router-link :to="'music'"><span @click="choose">音乐馆</span></router-link>
        <router-link :to="'audio'"><span @click="choose">发现</span></router-link>
      </div>
      <span><i class="fa fa-slideshare"></i></span>
    </section>
    <section id="search">
      <input type="text" />
      <span><i class="fa fa-search"></i>搜索</span>
    </section>
  </div>
</template>

<script>
  import { mapState, mapMutations } from 'vuex'
  export default {
    name: 'menu',
    components: {
    },
    data () {
      return {
      }
    },
    mounted () {
      let input = document.querySelectorAll('#search input')[0];
      let placeholder = document.querySelectorAll('#search>span')[0];
      input.addEventListener('click', function () {
        input.setAttribute('placeholder', '搜索音乐、歌词、歌单');
        placeholder.style.display = 'none';
      })
      input.addEventListener('blur', function () {
        if (input.value === '') {
          input.setAttribute('placeholder', '');
          placeholder.style.display = 'flex';
        }
      })
    },
    methods: {
      ...mapMutations({
        change: 'increment'
      }),
      choose (e) {
        for (let i of e.target.parentNode.parentNode.childNodes) {
          if (i.nodeType === 1) {
            i.firstChild.style.fontWeight = 'lighter';
          }
        };
        e.target.style.fontWeight = 'normal'
      }
    },
    computed: {
      ...mapState(['msg', 'count'])
    }
  }
</script>
<style rel="stylesheet/less" lang="less" scoped>
@import "../assets/less/var.less";
#topmenu{
  height: 103px;
  background: @green;
  #main{
    height: 61px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #fff;
    span:nth-child(1)>i{
       font-size: 1.5rem !important;
       margin: 0 0 0 13px;
    }
    span:nth-child(3)>i{
      font-size: 1.5rem !important;
      margin: 0 13px 0 0;
    }
    div{
      flex: 1;
      display: flex;
      a{
        flex: 1;
        text-align: center;
        font-size: 1.1rem;
        font-weight: lighter;
        color: #fff;
      }
      a:nth-child(2){
        font-weight: normal;
      }
    }
  }
  #search{
    padding: 5px 6px;
    height: 42px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    input{
      width: 100%;
      height: 100%;
      border: none;
      border-radius: 3px;
      background: #2baa6d;
      outline: none;
      font-size: 0.8rem;
      padding: 0 10px;
      color: #fff;
    }
    span{
      z-index: 90;
      text-align: center;
      font-size: 0.8rem;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%,50%);
      color: #fff;
      display: flex;
      justify-content: center;
      i{
        display: flex;
        align-items: center;
        margin-right: 5px;
      }
    }
  }
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color: #fff; opacity:1;
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #fff;opacity:1;
}

input:-ms-input-placeholder{
  color: #fff;opacity:1;
}

input::-webkit-input-placeholder{
  color: #fff;opacity:1;
}
</style>



