<template>
  <swiper :options="swiperOption1" id="hotslider">
    <!-- slides -->
    <template v-for="song in songlists">
      <swiper-slide v-show="!loading">
        <img :src="song.albumpic_big" @click="change(song)">
      </swiper-slide>
    </template>
    <img src="../assets/loader/bars.svg" class="loading" v-if="loading">
  </swiper>
</template>

<script>
  import { mapState, mapMutations } from 'vuex'
  export default {
    name: 'hotslider',
    data () {
      return {
        loading: true,
        songlists: [],
        swiperOption1: {
          autoplay: 4000,
          slidesPerView: 3,
          // loop: true,
          spaceBetween: 3,
          slidesPerColumn: 2
        }
      }
    },
    created () {
      let url = 'https://route.showapi.com/213-4';
      let type = [3, 4, 5, 6, 16, 17, 26, 27, 28, 32, 36];
      // 3:  欧美
      // 4:  流行榜
      // 5:  内地
      // 6:  港台
      // 16: 韩国
      // 17: 日本
      // 26: 热歌
      // 27: 新歌
      // 28: 网络歌曲
      // 32: 音乐人
      // 36: K歌金曲
      let n = Math.floor(Math.random() * type.length + 1) - 1;
      let topid = type[n];
      this.axios({
        url: url,
        method: 'get',
        responseType: 'json',
        params: {
          showapi_appid: 39121,
          topid: topid,
          showapi_sign: '8fd48a73b97f48c3a695e2963df3b18b'
        }
      }).then((data) => {
        this.songlists = data.data.showapi_res_body.pagebean.songlist.slice(0, 12);
        this.loading = false
      })
    },
    mounted  () {
    },
    methods: {
      change (song) {
        let music = {};
        music.songname = song.songname;
        music.singer = song.singername;
        music.musicUrl = song.url;
        music.musicPic = song.albumpic_big;
        let url = 'http://route.showapi.com/213-2';
        this.axios({
          url: url,
          method: 'get',
          responseType: 'json',
          params: {
            showapi_appid: 39121,
            musicid: song.songid,
            showapi_sign: '8fd48a73b97f48c3a695e2963df3b18b'
          }
        }).then((data) => {
          music.lyric = data.data.showapi_res_body.lyric;
        }).then(() => {
          this.$store.commit('changeMusic', music);
        });
      },
      ...mapMutations({
        changes: 'changeMusic'
      })
    },
    computed: {
      ...mapState(['music'])
    }
  }
</script>
<style rel="stylesheet/less" lang="less" scoped>
@import "../assets/less/var.less";
#hotslider{
  img{
    vertical-align: middle;
  }
  .loading{
    margin: 0 auto;
  }
}
</style>



