<template>
  <swiper :options="swiperOption" ref="mySwiper" id="slider">
    <!-- slides -->
    <template v-for="banner in banners">
      <swiper-slide class="bannerwrap">
        <a :href="banner.linkUrl"><img :src="banner.picUrl" class="bannerpic"></a>
      </swiper-slide>
    </template>
    <!-- Optional controls -->
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
  export default {
    name: 'slider',
    components: {
    },
    data () {
      return {
        banners: [],
        swiperOption: {
          autoplay: 4000,
          initialSlide: 1,
          loop: true,
          effect: 'fade',
          pagination: '.swiper-pagination',
          paginationClickable: true
        }
      }
    },
    created () {
      let url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg';
      let self = this;
      this.jsonp(url, { param: 'jsonpCallback' }, function (err, data) {
        if (err) {
          console.error(err.message)
        } else {
          self.banners = data.data.slider;
        }
      });
    },
    mounted  () {
    },
    methods: {
    },
    computed: {
    }
  }
</script>
<style rel="stylesheet/less" lang="less">
@import "../assets/less/var.less";
#slider{
  .bannerwrap{
    text-align: center;
    img{
      vertical-align:middle
    }
  }
  .swiper-pagination-bullet-active{
    background: #fff !important;
    opacity: 1;
  }
}
</style>



