<template>
  <div id="app">
    <!-- 主页面路由 -->
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <!-- 底部歌曲状态栏 -->
    <footer @click="showdetail">
      <div class="glass">
        <audio :src="musicUrl" autoplay loop></audio>
      </div>
      <section>
        <div id="songdetail">
          <span v-text="songname + '--'+ singer"></span>
          <span v-text="lineLyric"></span>
        </div>
        <div class="btngroup">
          <canvas id="process" width="50" height="50"></canvas>
          <div @click.stop="play" :class="[isplay? 'pause':'play']"></div>
        </div>
      </section>
    </footer>
    <!-- 旋转歌曲封面 -->
    <div id="btPic" :class="{myanimate:!isplay, normal:!show, show:show}" :style="{ background: 'url(' + musicPic + ') no-repeat center' }"></div>
    <!-- 点击底部弹出全屏歌曲信息 -->
    <transition name="bounce" enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">
      <section v-show="show">
        <div class="filter"></div>
        <div class="ablum">
          <header>
            <i class="fa fa-angle-down" @click.stop="showdetail"></i>{{songname}}
          </header>
          <songdetail :alllyric="lyricUl" :line="lineLyric"></songdetail>
          <section id="progress">
            <span class="time" v-text="nowtime"></span>
            <span class="pro"></span>
            <span class="btn"></span>
            <span class="probg"></span>
            <span class="time" v-text="totletime"></span>
          </section>
          <footer>
            <span class="pre"></span>
            <span :class="[isplay?'pause':'play']" @click.stop="play"></span>
            <span class="next"></span>
          </footer>
        </div>
      </section>
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import songdetail from '@/components/songdetail'
export default {
  name: 'app',
  components: {
    songdetail
  },
  create () {
      // 禁止ios用户缩放
    document.addEventListener('touchstart', function (event) {
      if (event.touches.length > 1) {
        event.preventDefault();
      }
    })
    var lastTouchEnd = 0;
    document.addEventListener('touchend', function (event) {
      var now = (new Date()).getTime();
      if (now - lastTouchEnd <= 300) {
        event.preventDefault();
      }
      lastTouchEnd = now;
    }, false)
  },
  data () {
    return {
      isplay: false,
      show: false,
      lineLyric: '',
      nowtime: '',
      totletime: ''
    }
  },
  mounted () {
    let myAudio = document.querySelectorAll('audio')[0];
    let self = this;
    myAudio.addEventListener('timeupdate', function () {
      self.nowtime = self.formattime(myAudio.currentTime);
      self.totletime = self.formattime(myAudio.duration);
      let alllyric = self.lyricUl;
      for (let i of alllyric) {
        if (myAudio.currentTime.toFixed(0) === i[0].toFixed(0)) {
          self.lineLyric = i[1]
        }
      }
      let percentNum = Math.floor((myAudio.currentTime / myAudio.duration) * 10000) / 100;
      let c = document.getElementById('process');
      let ctx = c.getContext('2d');
      self.drawCricle(ctx, percentNum);
      let progressbar = document.querySelectorAll('#progress>.btn')[0];
      let progress = document.querySelectorAll('#progress>.pro')[0];
      progress.style.width = `calc((100vw - 120px) * ${percentNum / 100})`;
      progressbar.style.left = `calc(55px + (100vw - 120px) * ${percentNum / 100})`;
    });
    let progressbar = document.querySelectorAll('#progress>.btn')[0];
    let progress = document.querySelectorAll('#progress>.pro')[0];
    progressbar.addEventListener('touchstart', function () {

    });
    progressbar.addEventListener('touchmove', function (e) {
      let totalwidth = document.body.offsetWidth - 120;
      let percentNum = ((e.targetTouches[0].pageX - 60) / totalwidth);
      if (percentNum > 1) {
        percentNum = 1;
      } else if (percentNum < 0) {
        percentNum = 0;
      }
      progressbar.style.left = `calc(55px + (100vw - 120px) * ${percentNum})`;
      progress.style.width = `calc((100vw - 120px) * ${percentNum})`;
    });
    progressbar.addEventListener('touchend', function (e) {
      let totalwidth = document.body.offsetWidth - 120;
      let percentNum = (e.changedTouches[0].pageX - 60) / totalwidth;
      progressbar.style.left = `calc(55px + (100vw - 120px) * ${percentNum})`;
      progress.style.width = `calc((100vw - 120px) * ${percentNum})`;
      myAudio.currentTime = myAudio.duration * percentNum;
    })
  },
  methods: {
    change () {
      this.$store.commit('changeMusic', this.music)
    },
    play () {
      let myAudio = document.querySelectorAll('audio')[0];
      // let pic = document.querySelectorAll('#btPic')[0];
      if (this.isplay) {
        myAudio.play();
        /* pic.style.animationPlayState = 'running';
        pic.style.mozAnimationPlayState = 'running';
        pic.style.oAnimationPlayState = 'running';
        pic.style.WebkitAnimationPlayState = 'running' */
      } else {
        myAudio.pause();
        /* pic.style.animationPlayState = 'paused';
        pic.style.mozAnimationPlayState = 'pause';
        pic.style.oAnimationPlayState = 'pause' */
      }
      this.isplay = !this.isplay
    },
    showdetail () {
      this.show = !this.show;
      // let pic = document.querySelectorAll('#btPic')[0];
      let glass = document.querySelectorAll('#app>section')[0];
      let mySwiper = document.querySelectorAll('#songslider')[0];
      let head = document.querySelectorAll('.ablum>header')[0];
      mySwiper.swiper.slideTo(0);
      mySwiper.swiper.update();
      glass.style.background = `url('${this.music[0].musicPic}') center no-repeat`;
      glass.style.backgroundSize = 'cover';
      if (this.show) {
        head.classList = ['animated fadeInDown'];
      } else {
        head.classList = [];
      }
    },
    formatlyric (lyric) {
      let t = document.createElement('div');
      t.innerHTML = lyric;
      let formatlyric = t.textContent || t.innerText;
      let lineArr = formatlyric.split('\n'); // 歌词为以排数为界的数组
      let timeReg = /\[\d{2}:\d{2}.\d{2}\]/g; // 时间的正则
      let result = [];
      for (let i in lineArr) {
        let time = lineArr[i].match(timeReg);
        if (!time) {
          continue;
        }
        var curStr = lineArr[i].replace(timeReg, '');
        for (let j in time) {
          let t = time[j].slice(1, -1).split(':'); // 时间的格式是[00:00.00] 分钟和毫秒是t[0],t[1]
          let curSecond = parseInt(t[0], 10) * 60 + parseFloat(t[1]);
          if (curStr === '') {
            break;
          }
          result.push([curSecond, curStr]);
        }
      }
      result.sort(function (a, b) {
        return a[0] - b[0];
      });
      return result;
    },
    drawCricle (ctx, percent) {
      ctx.clearRect(0, 0, 50, 50);
      ctx.beginPath();
      ctx.strokeStyle = '#4fc08d';
      ctx.lineWidth = '4';
      ctx.arc(25, 25, 18, Math.PI * 1.5, Math.PI * (1.5 + 2 * percent / 100));
      ctx.stroke();
    },
    formattime (time) {
      let min = Math.trunc(Math.trunc(time) / 60);
      let second = Math.trunc(time) % 60;
      if (second < 10) {
        second = '0' + second
      }
      if (min < 10) {
        min = '0' + min
      }
      time = min + ':' + second;
      return time
    },
    ...mapMutations(['changeMusic'])
  },
  computed: {
    ...mapState(['music', 'singer', 'songname', 'lyric', 'musicUrl', 'musicPic']),
    lyricUl () {
      return this.formatlyric(this.lyric)
    }
  }
}
</script>

<style rel="stylesheet/less" lang="less">
@import "assets/less/var.less";
html{
  font-size: 20px !important;
}
*{
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
  -moz-tap-highlight-color:rgba(0, 0, 0, 0);
  -ms-tap-highlight-color:rgba(0, 0, 0, 0);
  -o-tap-highlight-color:rgba(0, 0, 0, 0);
  tap-highlight-color:rgba(0, 0, 0, 0);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 0.7rem;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  >footer {
    height: 56px;
    width: 100vw;
    position: fixed;
    bottom: 0;
    background: rgba(255,255,255,0.9);
    border-top: 1px solid #3c3c48;
    z-index: 90;
    /*border-bottom: 1px solid #3c3c48;*/
    .glass{

    }
    section {
      position: absolute;
      z-index: 22;
      width: 100vw;
      height: 100%;
      padding: 0 10px 0 60px;
      top: 0;
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      #songdetail{
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        padding: 0 10px;
        flex: 1;
        overflow:hidden;
        span{
          flex: 1;
          display: inline-block;
          line-height: 1rem;
          height: 1rem;
        }
        span:nth-child(2){
          display:block;
          white-space:nowrap;
          overflow:hidden;
          text-overflow:ellipsis;
          color: @green;
        }
      }
      .btngroup{
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        div{
          position: absolute;
          top:0;
          width: 50px;
          height: 50px;
          left: 50%;
          transform: translate(-50%,0);
        }
        div.play{
          background: url("assets/img/pause.svg") no-repeat center;
          background-size: 38px 38px;
        }
        div.pause{
          background: url("assets/img/menu-pause.svg") no-repeat center;
          background-size: 38px 38px;
        }
      }
    }
  }
  #btPic.normal {
    width: 50px;
    height: 50px;
    position: fixed;
    z-index: 999;
    bottom: 0;
    left: 10px;
    transition: all .3s linear;
    border-radius: 50%;
    background-size: contain !important;
    /*animation: rotate 25s infinite linear;*/
    /*transition: all 1s linear;*/
  }
  #btPic.show{
    position: fixed;
    z-index: 999;
    width: 70vw;
    height: 70vw;
    bottom: calc(~'50vh - 20vw');
    left: calc(~'50vw - 35vw ');
    box-shadow: #000 0 0 20px;
    transition: all .3s linear;
    border-radius: 50%;
    background-size: contain !important;
    /*animation: rotate 25s infinite linear;*/
    /*translate .8s linear forwards;*/
  }
  #btPic.myanimate{
    animation: rotate 25s infinite linear;
    animation-fill-mode: forwards;
  }
  >section{
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    z-index: 98;
    background: rgba(0, 0, 0, 0.7);
    overflow: hidden;
    .filter{
      width: 130%;
      height: 130%;
      background: inherit;
      margin: -30px;
      -webkit-filter: blur(25px);
      -moz-filter: blur(25px);
      -ms-filter: blur(25px);
      -o-filter: blur(25px);
      filter: blur(25px);
      filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius=4, MakeShadow=false);
    }
    .ablum{
      position: absolute;
      top: 0;
      width: 100vw;
      height: 100vh;
      display: flex;
      flex-direction: column;
      header{
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #fff;
        font-weight: lighter;
        font-size: 1.25rem;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        i{
          position: absolute;
          font-size: 2rem;
          left: 20px;
        }
        &.animated{
          animation-delay: 0.3s
        }
      }
      section#progress{
        margin-bottom: 0;
        height: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgba(255, 255, 255, .5);
        position: relative;
        span.time{
          display: inline-block;
          text-align: center;
          width: 60px;
          padding: 0 12px;
        }
        span.pro{
          height: 3px;
          background: @green;
          /*width: calc(~'(100vw - 122px) * 0.5');*/
        }
        span.probg{
          flex: 1;
          height: 3px;
          background: rgba(255, 255, 255, .5);
        }
        span.btn{
          display: inline-block;
          width: 10px;
          height: 10px;
          left: 55px;
          top:calc(~'50% - 5px');
          border-radius: 50%;
          background: @green;
          position: absolute;
        }
      }
      footer{
        height: 80px;
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
        span{
          display: inline-block;
          margin: 0 1rem;
          &.pre{
            background: url("assets/img/pre.svg") no-repeat center;
            background-size: 46px 46px;
            width: 46px;
            height: 46px;
          }
          &.pause{
            background: url("assets/img/play.svg") no-repeat center;
            background-size: 60px 60px;
            width: 60px;
            height: 60px;
          }
          &.play{
            background: url("assets/img/pause.svg") no-repeat center;
            background-size: 60px 60px;
            width: 60px;
            height: 60px;
          }
          &.next{
            background: url("assets/img/next.svg") no-repeat center;
            background-size: 46px 46px;
            width: 46px;
            height: 46px;
          }
        }
      }
    }
  }
}
@keyframes rotate {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
@keyframes translate {
  0%{
    transform: translate(0,0);
    width: 50px;
    height: 50px;
    bottom: 0;
  }
  100%{
    width: 260px;
    height: 260px;
    /*transform: translate(calc(~'50vw - 140px'),calc(~'-50vh - 65px'));*/
    transform: translate(100px,100px);
  }
}
</style>



