import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import music from '@/pages/index/music'
import mine from '@/pages/index/mine'
import audio from '@/pages/index/audio'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      redirect: '/'
    },
    {
      path: '/',
      component: index,
      redirect: '/index/music',
      children: [
        {
          name: 'music',
          path: '/index/music',
          component: music
        },
        {
          name: 'mine',
          path: '/index/mine',
          component: mine
        },
        {
          name: 'audio',
          path: '/index/audio',
          component: audio
        }
      ]
    }
  ]
})



